.mainWrapper{
    height:100vh;
    width:100vw;
    overflow:none;
    background:url(/reactPortfolio/static/media/background-general.e06fab6d.jpg);
    background-size:cover;
}

body{
    overflow:hidden;
}

.main{
    position:absolute;
    top:10vh;
    left:2.5vh;
    height:-webkit-fit-content !important;
    height:-moz-fit-content !important;
    height:fit-content !important;
}

.contain-navbar{
    
    height: 10vh;
    position: absolute;
    top: 182px;
    left: 28px;
    -webkit-transform: rotate(30deg);
    transform: rotate(30deg);
    font-weight: 600;
    font-size: 21px;
}

.menu-button{
    color:white;
    display:inline-block;
    padding:10px;
    margin:4px;
    -webkit-animation: shadow 1s infinite;
            animation: shadow 1s infinite;
}
/*styling for project and about me modal */
.contain-navbar-modal{
    font-weight: 600;
    font-size: 21px;
}

.menu-button-modal{
    display:inline;
    font-size:10px;
    color:black;
    padding:10px;
    margin:4px;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    -webkit-animation: shadow 1s infinite;
            animation: shadow 1s infinite;
}


.projects{
    -webkit-animation-delay:0.33s;
            animation-delay:0.33s;
}
.about-me{
    -webkit-animation-delay:0.66s;
            animation-delay:0.66s;
}

.inactive{
    opacity:0.5
}


@-webkit-keyframes shadow{
    50%{box-shadow:5px 10px black}
    100%{box-shadow:0px 0px black}
}


@keyframes shadow{
    50%{box-shadow:5px 10px black}
    100%{box-shadow:0px 0px black}
}

@-webkit-keyframes transition{
    50%{
        -webkit-transform:rotate(-180deg);
                transform:rotate(-180deg);
    }
}

@keyframes transition{
    50%{
        -webkit-transform:rotate(-180deg);
                transform:rotate(-180deg);
    }
}
.main{
    height:80vh;
    width:90vw;
    background:white;
    opacity:1;
    margin:auto;
}

.project-space{
    height:20vh;
    margin:15px;
}

.contain-projects{
    height:-webkit-fill-available;
    height:-moz-available;
    height:stretch;
    overflow:scroll;
}


@-webkit-keyframes fadeIn{
    from{opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);}
    To{opacity:1;-webkit-transform:none;transform:none}
}


@keyframes fadeIn{
    from{opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);}
    To{opacity:1;-webkit-transform:none;transform:none}
}

@media only screen and (max-width: 400px)  {
    .text{
        font-size:0.8em
    }

}
